export default {
	data() {
		return {
			isHidePassword:true,
			ordpasswd:"",
			password:""
		};
	},
	onLoad() {
	},
	methods: {
		isHidePasswordClick(){
			this.isHidePassword=!this.isHidePassword
		},
		submit() {
			uni.request({
				url: this.apiServer+'/api/login/pass',
				method: 'post',
				data: {
					ordpasswd:this.ordpasswd,
					user_id:uni.getStorageSync('USERID'),
					passwd:this.passwd
				},
				success: res => {
					if(res.data.err==0){
						uni.showToast({
						    title: '修改成功',
						    duration: 2000
						});
					}else{
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000
						});
					}
				}

			});
		}
	}
}
